      <p class="page-description">æ”¯æŒé•¿åº¦ã€é‡é‡ã€æ¸©åº¦ã€é¢ç§¯ã€ä½“ç§¯ã€åŠŸç‡ã€å­˜å‚¨ç­‰å¤šç§å•ä½è½¬æ¢</p>
    </div>

    <div class="converter-section">
      <div class="category-selector">
        <el-tabs v-model="activeCategory" @tab-click="resetValues">
          <el-tab-pane label="ğŸ“ é•¿åº¦" name="length"></el-tab-pane>
          <el-tab-pane label="âš–ï¸ é‡é‡" name="weight"></el-tab-pane>
          <el-tab-pane label="ğŸŒ¡ï¸ æ¸©åº¦" name="temperature"></el-tab-pane>
          <el-tab-pane label="ğŸ“¦ ä½“ç§¯" name="volume"></el-tab-pane>
          <el-tab-pane label="ğŸ“ é¢ç§¯" name="area"></el-tab-pane>
          <el-tab-pane label="âš¡ åŠŸç‡" name="power"></el-tab-pane>
          <el-tab-pane label="ğŸ’¾ å­˜å‚¨" name="storage"></el-tab-pane>
        </el-tabs>
      </div>

      <div class="conversion-panel">
        <div class="input-section">
          <h3>è¾“å…¥å€¼</h3>
          <div class="input-group">
            <el-input-number
              v-model="inputValue"
              :precision="6"
              :step="1"
              @change="convert"
              size="large"
              style="width: 200px"
            ></el-input-number>
            <el-select v-model="fromUnit" @change="convert" size="large">
              <el-option
                v-for="unit in getCurrentUnits()"
                :key="unit.key"
                :label="unit.name"
                :value="unit.key"
              ></el-option>
            </el-select>
          </div>
        </div>

        <div class="conversion-arrow">
          <i class="el-icon-right"></i>
        </div>

        <div class="output-section">
          <h3>è½¬æ¢ç»“æœ</h3>
          <div class="results-list">
            <div
              v-for="unit in getCurrentUnits()"
              :key="unit.key"
              class="result-item"
              :class="{ active: unit.key === fromUnit }"
            >
              <div class="result-label">{{ unit.name }}</div>
              <div class="result-value">
                {{ formatResult(convertValue(inputValue, fromUnit, unit.key)) }}
              </div>
              <div class="result-symbol">{{ unit.symbol }}</div>
              <el-button
                size="mini"
                @click="copyResult(convertValue(inputValue, fromUnit, unit.key), unit.symbol)"
              >
                å¤åˆ¶
              </el-button>
            </div>
          </div>
        </div>
      </div>

      <div class="quick-conversions" v-if="getQuickConversions().length > 0">
        <h3>ğŸ”¥ å¸¸ç”¨è½¬æ¢</h3>
        <div class="quick-buttons">
          <el-button
            v-for="quick in getQuickConversions()"
            :key="quick.label"
            size="small"
            @click="setQuickConversion(quick)"
          >
            {{ quick.label }}
          </el-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ClipboardUtil } from '@/util/ClipboardUtil'

export default {
  name: 'UnitConverterView',
  data() {
    return {
      activeCategory: 'length',
      inputValue: 1,
      fromUnit: 'meter',
      
      units: {
        length: [
          { key: 'meter', name: 'ç±³', symbol: 'm', factor: 1 },
          { key: 'kilometer', name: 'åƒç±³', symbol: 'km', factor: 1000 },
          { key: 'centimeter', name: 'å˜ç±³', symbol: 'cm', factor: 0.01 },
          { key: 'millimeter', name: 'æ¯«ç±³', symbol: 'mm', factor: 0.001 },
          { key: 'inch', name: 'è‹±å¯¸', symbol: 'in', factor: 0.0254 },
          { key: 'foot', name: 'è‹±å°º', symbol: 'ft', factor: 0.3048 },
          { key: 'yard', name: 'ç ', symbol: 'yd', factor: 0.9144 },
          { key: 'mile', name: 'è‹±é‡Œ', symbol: 'mi', factor: 1609.344 }
        ],
        weight: [
          { key: 'kilogram', name: 'åƒå…‹', symbol: 'kg', factor: 1 },
          { key: 'gram', name: 'å…‹', symbol: 'g', factor: 0.001 },
          { key: 'pound', name: 'ç£…', symbol: 'lb', factor: 0.453592 },
          { key: 'ounce', name: 'ç›å¸', symbol: 'oz', factor: 0.0283495 },
          { key: 'ton', name: 'å¨', symbol: 't', factor: 1000 },
          { key: 'stone', name: 'è‹±çŸ³', symbol: 'st', factor: 6.35029 }
        ],
        temperature: [
          { key: 'celsius', name: 'æ‘„æ°åº¦', symbol: 'Â°C' },
          { key: 'fahrenheit', name: 'åæ°åº¦', symbol: 'Â°F' },
          { key: 'kelvin', name: 'å¼€å°”æ–‡', symbol: 'K' }
        ],
        volume: [
          { key: 'liter', name: 'å‡', symbol: 'L', factor: 1 },
          { key: 'milliliter', name: 'æ¯«å‡', symbol: 'mL', factor: 0.001 },
          { key: 'gallon', name: 'åŠ ä»‘', symbol: 'gal', factor: 3.78541 },
          { key: 'quart', name: 'å¤¸è„±', symbol: 'qt', factor: 0.946353 },
          { key: 'pint', name: 'å“è„±', symbol: 'pt', factor: 0.473176 },
          { key: 'cup', name: 'æ¯', symbol: 'cup', factor: 0.236588 }
        ],
        area: [
          { key: 'square_meter', name: 'å¹³æ–¹ç±³', symbol: 'mÂ²', factor: 1 },
          { key: 'square_kilometer', name: 'å¹³æ–¹åƒç±³', symbol: 'kmÂ²', factor: 1000000 },
          { key: 'square_centimeter', name: 'å¹³æ–¹å˜ç±³', symbol: 'cmÂ²', factor: 0.0001 },
          { key: 'hectare', name: 'å…¬é¡·', symbol: 'ha', factor: 10000 },
          { key: 'acre', name: 'è‹±äº©', symbol: 'ac', factor: 4046.86 },
          { key: 'square_foot', name: 'å¹³æ–¹è‹±å°º', symbol: 'ftÂ²', factor: 0.092903 }
        ],
        power: [
          { key: 'watt', name: 'ç“¦ç‰¹', symbol: 'W', factor: 1 },
          { key: 'kilowatt', name: 'åƒç“¦', symbol: 'kW', factor: 1000 },
          { key: 'horsepower', name: 'é©¬åŠ›', symbol: 'hp', factor: 745.7 },
          { key: 'btu_per_hour', name: 'BTU/å°æ—¶', symbol: 'BTU/h', factor: 0.293071 }
        ],
        storage: [
          { key: 'byte', name: 'å­—èŠ‚', symbol: 'B', factor: 1 },
          { key: 'kilobyte', name: 'åƒå­—èŠ‚', symbol: 'KB', factor: 1024 },
          { key: 'megabyte', name: 'å…†å­—èŠ‚', symbol: 'MB', factor: 1024 * 1024 },
          { key: 'gigabyte', name: 'å‰å­—èŠ‚', symbol: 'GB', factor: 1024 * 1024 * 1024 },
          { key: 'terabyte', name: 'å¤ªå­—èŠ‚', symbol: 'TB', factor: 1024 * 1024 * 1024 * 1024 },
          { key: 'petabyte', name: 'æ‹å­—èŠ‚', symbol: 'PB', factor: 1024 * 1024 * 1024 * 1024 * 1024 },
          { key: 'bit', name: 'ä½', symbol: 'bit', factor: 0.125 },
          { key: 'kilobit', name: 'åƒä½', symbol: 'Kbit', factor: 128 },
          { key: 'megabit', name: 'å…†ä½', symbol: 'Mbit', factor: 128 * 1024 },
          { key: 'gigabit', name: 'å‰ä½', symbol: 'Gbit', factor: 128 * 1024 * 1024 }
        ]
      },
      
      quickConversions: {
        length: [
          { label: '1ç±³ = ? è‹±å°º', value: 1, from: 'meter', to: 'foot' },
          { label: '1è‹±å¯¸ = ? å˜ç±³', value: 1, from: 'inch', to: 'centimeter' },
          { label: '1åƒç±³ = ? è‹±é‡Œ', value: 1, from: 'kilometer', to: 'mile' }
        ],
        weight: [
          { label: '1åƒå…‹ = ? ç£…', value: 1, from: 'kilogram', to: 'pound' },
          { label: '1ç£… = ? åƒå…‹', value: 1, from: 'pound', to: 'kilogram' }
        ],
        temperature: [
          { label: '0Â°C = ? Â°F', value: 0, from: 'celsius', to: 'fahrenheit' },
          { label: '100Â°C = ? Â°F', value: 100, from: 'celsius', to: 'fahrenheit' }
        ],
        storage: [
          { label: '1GB = ? MB', value: 1, from: 'gigabyte', to: 'megabyte' },
          { label: '1TB = ? GB', value: 1, from: 'terabyte', to: 'gigabyte' },
          { label: '1MB = ? KB', value: 1, from: 'megabyte', to: 'kilobyte' },
          { label: '8bit = ? B', value: 8, from: 'bit', to: 'byte' }
        ]
      }
    }
  },
  mounted() {
    this.resetValues()
  },
  methods: {
    getCurrentUnits() {
      return this.units[this.activeCategory] || []
    },
    
    getQuickConversions() {
      return this.quickConversions[this.activeCategory] || []
    },
    
    resetValues() {
      const units = this.getCurrentUnits()
      if (units.length > 0) {
        this.fromUnit = units[0].key
        this.inputValue = 1
      }
    },
    
    convert() {
      // è§¦å‘é‡æ–°è®¡ç®—
      this.$forceUpdate()
    },
    
    convertValue(value, fromUnit, toUnit) {
      if (!value || fromUnit === toUnit) return value
      
      if (this.activeCategory === 'temperature') {
        return this.convertTemperature(value, fromUnit, toUnit)
      }
      
      const fromUnitData = this.getCurrentUnits().find(u => u.key === fromUnit)
      const toUnitData = this.getCurrentUnits().find(u => u.key === toUnit)
      
      if (!fromUnitData || !toUnitData) return value
      
      // å…ˆè½¬æ¢ä¸ºåŸºå‡†å•ä½ï¼Œå†è½¬æ¢ä¸ºç›®æ ‡å•ä½
      const baseValue = value * fromUnitData.factor
      return baseValue / toUnitData.factor
    },
    
    convertTemperature(value, fromUnit, toUnit) {
      let celsius = value
      
      // è½¬æ¢ä¸ºæ‘„æ°åº¦
      if (fromUnit === 'fahrenheit') {
        celsius = (value - 32) * 5 / 9
      } else if (fromUnit === 'kelvin') {
        celsius = value - 273.15
      }
      
      // ä»æ‘„æ°åº¦è½¬æ¢ä¸ºç›®æ ‡å•ä½
      if (toUnit === 'fahrenheit') {
        return celsius * 9 / 5 + 32
      } else if (toUnit === 'kelvin') {
        return celsius + 273.15
      }
      
      return celsius
    },
    
    formatResult(value) {
      if (typeof value !== 'number' || isNaN(value)) return '0'
      
      if (Math.abs(value) >= 1000000) {
        return value.toExponential(3)
      } else if (Math.abs(value) >= 1000) {
        return value.toLocaleString('zh-CN', { maximumFractionDigits: 3 })
      } else if (Math.abs(value) >= 1) {
        return value.toFixed(6).replace(/\.?0+$/, '')
      } else {
        return value.toFixed(8).replace(/\.?0+$/, '')
      }
    },
    
    async copyResult(value, symbol) {
      const formattedValue = this.formatResult(value)
      const text = `${formattedValue} ${symbol}`

      try {
        const success = await ClipboardUtil.copyText(text)
        if (success) {
          this.$message.success(`å·²å¤åˆ¶: ${text}`)
        } else {
          this.$message.error('å¤åˆ¶å¤±è´¥')
        }
      } catch (error) {
        this.$message.error('å¤åˆ¶å¤±è´¥: ' + error.message)
      }
    },
    
    setQuickConversion(quick) {
      this.inputValue = quick.value
      this.fromUnit = quick.from
      this.convert()
    }
  }
}
</script>

